<template>
  <div>
    <el-row :gutter="20">
      <el-col class="mb20" :xl="6" :lg="6" :md="10" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>自定义表单</span>
          </div>
          <el-form :model="editParams" label-position="right" label-width="80px">
            <el-form-item label="表单名称">
              <el-input v-model="editParams.name" autocomplete="off" placeholder="名称：建议2-4字以内"></el-input>
            </el-form-item>
            <el-form-item label="表名称">
              <el-input
                v-model="editParams.tableName"
                :disabled="editParams.id != null"
                autocomplete="off"
                placeholder="以字母开头，下划线分隔，例：message"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="saveFormField">保存</el-button>
              <el-button @click="resetForm">重置</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
      <el-col class="mb20" :xl="18" :lg="18" :md="14" :sm="24" :xs="24">
        <el-card>
          <div slot="header" class="clearfix">
            <span>所有表单</span>
          </div>
          <el-table
            :data="forms"
            style="margin-bottom:20px;"
            v-loading="tableLoading"
            element-loading-background="rgba(0, 0, 0, 0.3)"
            element-loading-text="数据正在加载中"
            element-loading-spinner="el-icon-loading"
          >
            <el-table-column prop="id" label="ID" width="60"></el-table-column>
            <el-table-column prop="name" label="表单名称"></el-table-column>
            <el-table-column prop="tableName" label="表名称"></el-table-column>
            <el-table-column label="数据" width="150">
              <template slot-scope="scope">
                <el-button type="text" @click="viewData(scope.row)">查看数据</el-button>
              </template>
            </el-table-column>
            <el-table-column label="字段" width="150">
              <template slot-scope="scope">
                <el-button type="text" @click="showField(scope.row)">编辑字段</el-button>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="150">
              <template slot-scope="scope">
                <el-button type="text" @click="edit(scope.row)">修改</el-button>
                <el-popconfirm
                  title="确定删除本条数据吗？删除后不可恢复。"
                  style="margin-left:10px;"
                  cancelButtonType="default"
                  @onConfirm="del(scope.row)"
                >
                  <el-button type="text" slot="reference">删除</el-button>
                </el-popconfirm>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>
    <el-dialog class="fieldDialog" width="800px" :visible.sync="showFormField">
      <el-tabs v-model="fieldTab" @tab-click="tabClick">
        <el-tab-pane label="字段列表" name="fieldTable">
          <el-table
            :data="fields"
            style="margin-bottom:20px;"
            element-loading-background="rgba(0, 0, 0, 0.3)"
            element-loading-text="数据正在加载中"
            element-loading-spinner="el-icon-loading"
          >
            <el-table-column prop="id" label="ID" width="60"></el-table-column>
            <el-table-column prop="name" label="名称"></el-table-column>
            <el-table-column prop="field" label="字段"></el-table-column>
            <el-table-column prop="type" label="类型"></el-table-column>
            <el-table-column prop="size" label="长度"></el-table-column>
            <el-table-column prop="value" label="字段值" show-overflow-tooltip></el-table-column>
            <el-table-column label="操作" width="150">
              <template slot-scope="scope">
                <el-button
                  type="text"
                  @click="tableFieldParams = scope.row;fieldTab = 'fieldEdit'"
                >修改</el-button>
                <el-popconfirm
                  title="确定删除本条数据吗？删除后不可恢复。"
                  style="margin-left:10px;"
                  cancelButtonType="default"
                  @onConfirm="delField(scope.row)"
                >
                  <el-button type="text" slot="reference">删除</el-button>
                </el-popconfirm>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="字段编辑" name="fieldEdit">
          <el-form :model="tableFieldParams" label-position="right" label-width="80px">
            <el-form-item label="名称">
              <el-input
                v-model="tableFieldParams.name"
                autocomplete="off"
                placeholder="名称：建议2-4字以内"
              ></el-input>
            </el-form-item>
            <el-form-item label="字段">
              <el-input
                v-model="tableFieldParams.field"
                :disabled="tableFieldParams.id != null"
                autocomplete="off"
                placeholder="以字母开头，下划线分隔，例：price"
              ></el-input>
            </el-form-item>
            <el-form-item label="类型">
              <el-select
                v-model="tableFieldParams.type"
                :disabled="tableFieldParams.id != null"
                placeholder="请选类型"
              >
                <el-option label="单行文本" value="input"></el-option>
                <el-option label="多行文本" value="textarea"></el-option>
                <el-option label="下拉列表" value="select"></el-option>
                <el-option label="日期选择" value="date"></el-option>
                <el-option label="单选按钮" value="radio"></el-option>
                <el-option label="多选按钮" value="checkbox"></el-option>
                <el-option label="图片上传" value="image"></el-option>
                <el-option label="附件上传" value="file"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="长度">
              <el-input
                v-model="tableFieldParams.size"
                autocomplete="off"
                :disabled="tableFieldParams.id != null"
                placeholder="字段长度"
              ></el-input>
            </el-form-item>
            <el-form-item label="字段值">
              <el-input
                v-model="tableFieldParams.value"
                autocomplete="off"
                placeholder="只在单选、多选、下拉选择时有效"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="saveFiled">保存</el-button>
              <el-button>取消</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
    <el-dialog class="formDataDialog" title="表单数据" width="800px" :visible.sync="showViewData">
      <el-table
        :data="formData"
        style="margin-bottom:20px;"
        element-loading-background="rgba(0, 0, 0, 0.3)"
        element-loading-text="数据正在加载中"
        element-loading-spinner="el-icon-loading"
      >
        <el-table-column prop="id" label="ID" width="60"></el-table-column>
        <el-table-column
          v-for="field in fields"
          :key="field.field"
          :label="field.name"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <el-image
              v-if="field.type=='image' && scope.row[field.field]"
              style="height: 80px"
              :src="scope.row[field.field]"
              :fit="scale-down"
              :preview-src-list="[scope.row[field.field]]"
            ></el-image>
            <el-link
              v-else-if="field.type=='file' && scope.row[field.field]"
              type="primary"
              :href="scope.row[field.field]"
              target="_blank"
              :underline="false"
            >查看</el-link>
            <span v-else>{{scope.row[field.field]}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <el-button type="text">修改</el-button>
            <el-popconfirm
              title="确定删除本条数据吗？删除后不可恢复。"
              style="margin-left:10px;"
              cancelButtonType="default"
              @onConfirm="delFormData(scope.row)"
            >
              <el-button type="text" slot="reference">删除</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <pagination
        @sizeChange="handleSizeChange"
        @currentChange="handleCurrentChange"
        :pageNum="queryParams.pageNum"
        :pageSize="queryParams.pageSize"
        :total="formDataTotal"
      ></pagination>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/pagination";
import formApi from "@/api/formApi";
import formFieldApi from "@/api/formFieldApi";
export default {
  components: { Pagination },
  data() {
    return {
      editParams: {},
      tableLoading: false,
      forms: [],
      tableFieldParams: { size: 255, type: "input" },
      fieldTab: "fieldTable",
      selectedTable: {},
      showFormField: false,
      showViewData: false,
      formData: [],
      queryParams: {
        pageNum: 1,
        pageSize: 5
      },
      formDataTotal: 0,
      fields: []
    };
  },
  created() {
    this.getForms();
  },
  methods: {
    getForms() {
      this.tableLoading = true;
      formApi.findAll().then(res => {
        this.forms = res.data;
        this.tableLoading = false;
      });
    },
    typeChange(val) {},
    saveFormField() {
      if (!this.editParams.name || !this.editParams.tableName) {
        this.$message.warning("请填写表单名称和表名称");
        return false;
      }
      if (this.editParams.id) {
        formApi.update(this.editParams).then(res => {
          this.$message.success("保存成功");
          this.getForms();
        });
      } else {
        formApi.create(this.editParams).then(res => {
          this.$message.success("保存成功");
          this.getForms();
        });
      }
    },
    edit(row) {
      this.editParams = row;
    },
    del(row) {
      formApi.delete(row.id).then(res => {
        this.$message.success("删除成功");
        this.getForms();
      });
    },
    showField(row) {
      this.selectedTable = row;
      this.showFormField = true;
      this.getAllField(row.tableName);
    },
    viewData(row) {
      this.selectedTable = row;
      this.getAllField(row.tableName);
      this.getAllData(row.tableName);
    },
    getAllField(tableName) {
      formFieldApi.findAll({ tableName: tableName }).then(res => {
        this.fields = res.data;
      });
    },
    getAllData(tableName) {
      this.queryParams.tableName = tableName;
      formApi.getFormData(this.queryParams).then(res => {
        this.showViewData = true;
        this.formData = res.data.records;
        this.formDataTotal = res.data.total;
      });
    },
    tabClick(tab) {
      this.tableFieldParams = {
        size: 255,
        type: "input",
        tableName: this.selectedTable.tableName
      };
    },
    saveFiled() {
      let tableName = this.selectedTable.tableName;
      this.tableFieldParams.tableName = tableName;
      if (!this.tableFieldParams.name) {
        this.$message.warning("请填写名称");
        return false;
      }
      if (!this.tableFieldParams.field) {
        this.$message.warning("请填写字段");
        return false;
      }
      if (this.tableFieldParams.id) {
        formFieldApi.update(this.tableFieldParams).then(res => {
          this.getAllField(tableName);
          this.fieldTab = "fieldTable";
        });
      } else {
        formFieldApi.create(this.tableFieldParams).then(res => {
          this.getAllField(tableName);
          this.fieldTab = "fieldTable";
        });
      }
    },
    delField(row) {
      formFieldApi.delete(row.id).then(res => {
        this.getAllField(row.tableName);
        this.fieldTab = "fieldTable";
      });
    },
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getAllData(this.selectedTable.tableName);
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getAllData(this.selectedTable.tableName);
    },
    delFormData() {},
    resetForm() {
      this.editParams = {};
    }
  }
};
</script>

<style lang="scss">
.fieldDialog {
  .el-dialog {
    min-height: 500px;
    .el-dialog__body {
      padding-top: 0;
    }
  }
}
.formDataDialog {
  .el-dialog__body {
    min-height: 425px;
  }
}
</style>>
