import request from '@/utils/request'

const baseUrl = '/api/admin/formField'

const formFieldApi = {}

formFieldApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

formFieldApi.page = (params) => {
  return request({
    url: `${baseUrl}/page`,
    params: params,
    method: 'get'
  })
}

formFieldApi.get = formFieldId => {
  return request({
    url: `${baseUrl}/${formFieldId}`,
    method: 'get'
  })
}

formFieldApi.update = (params) => {
  return request({
    url: `${baseUrl}`,
    data: params,
    method: 'put'
  })
}

formFieldApi.create = formField => {
  return request({
    url: `${baseUrl}`,
    data: formField,
    method: 'post'
  })
}

formFieldApi.delete = formFieldId => {
  return request({
    url: `${baseUrl}/${formFieldId}`,
    method: 'delete'
  })
}

export default formFieldApi
