import request from '@/utils/request'

const baseUrl = '/api/admin/form'

const formApi = {}

formApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

formApi.page = (params) => {
  return request({
    url: `${baseUrl}/page`,
    params: params,
    method: 'get'
  })
}

formApi.get = formId => {
  return request({
    url: `${baseUrl}/${formId}`,
    method: 'get'
  })
}

formApi.update = (params) => {
  return request({
    url: `${baseUrl}/update`,
    data: params,
    method: 'put'
  })
}

formApi.create = form => {
  return request({
    url: `${baseUrl}/save`,
    data: form,
    method: 'post'
  })
}

formApi.delete = formId => {
  return request({
    url: `${baseUrl}/${formId}`,
    method: 'delete'
  })
}

formApi.getFormData = (params) => {
  return request({
    url: `${baseUrl}/data`,
    params: params,
    method: 'get'
  })
}

export default formApi
